@import '../technical/colors.scss';

.section-title {
    margin: 0;
    font-weight: normal;
    text-transform: uppercase;
    font-size: 16px;
    line-height: 24px;
    background-color: $white;
    border-top: 1px solid $light-green;
    padding: 16px;
    padding-top: 15px;
}

@media (min-width: 768px) {
    .section-title {
        padding: 16px 24px;
        padding-top: 15px;

    }

}
